@mixin onTablet{
  @media(min-width: $tablet-min-width){
    @content;
   }
}

@mixin onDesktop{
  @media(min-width: $desktop-min-width){
    @content;
   }
}

@mixin contentPaddingInline() {
  padding-inline: 20px;

  @include onTablet{
    padding-inline: 72px;
  }

  @include onDesktop(){
    max-width: 1200px;
    margin-inline: auto;
    padding-inline: 180px;
  }
}

.container{
  @include contentPaddingInline;
}

@mixin hover ($property, $toValue){
  transition: #{$property} 0.3s;
  &:hover{
    #{$property}:$toValue;
  }
}

@mixin pageGrid{
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns),1fr);

  @include onTablet{
   --columns: 6;
   column-gap: 24px;
  }

  @include onDesktop{
   --columns: 12;
  }
}
