.nav{
  &__list{
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link{
    position: relative;
    color: $black-color;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    &::after{
      content: '';
      position: absolute;
      bottom: -8px;
      display: block;
      height: 1px;
      width: 100%;
      background-color: $black-color;

      transition: transform 0.3s;
      transform: scale(0);
    }

    &:hover::after{
      transform: scale(1);
    }

    }
}
